import React from 'react';
import {Card,Table} from 'react-bootstrap';
import {useSelector} from "react-redux";
import {Navigate} from "react-router-dom";


export default function Home() {
  let storeDetails=useSelector((store)=>store)
  if(storeDetails.userDetails.loggedIn==="yes"){
    console.log("you visit this Page")
  }else{
    console.log("You Wont Visit this Page")
    return <Navigate to="/"/>
  }
  

  return (
    
  <div>
   {storeDetails.userDetails?<div>  <Card style={{ width: '18rem' }}>
   <Card.Img style={{width:"200px",height:"200px"}} variant="top" src= {'https://pulse.brninfotech.com/pulse/modules/modules/'+storeDetails.userDetails.profileImagePath} alt="image"/>
   <Card.Body>
    <Card.Title>Student Photo</Card.Title>
    <Card.Text>
      <b>Batchid:</b>{storeDetails.userDetails.batchID}
      <br/>
      <b>firstName:</b>{storeDetails.userDetails.firstName}
      <br/>
      <b>role:</b>{storeDetails.userDetails.role}

    </Card.Text>
  </Card.Body>
</Card>

<p style={{font:"2rem",color:"yellow",backgroundColor:"crimson"}}>Attedence Summary</p>
<Table striped bordered hover>
    
  <tbody>
    <tr>
      <td>No.of Classes</td>
      <td>120</td>
       </tr>
    <tr>
      <td>Present Classes</td>
      <td>60</td>
       </tr>
    <tr>
      <td>Absent Classes</td>
      <td>30</td>
      </tr>
  </tbody>
</Table></div>: "null"}
    </div>
  )
}
