import { React,useRef } from "react";
import { Form, Row, Col,Button,Modal } from "react-bootstrap";
import { useNavigate } from 'react-router-dom';
import { useDispatch } from "react-redux";



export default function Login() {
    let navigate=useNavigate("");
    let receivePassword=useRef("");
    let receiveDetailsName=useRef("");
    let dispatch=useDispatch("")

    
    

    const serverResponse=async()=>{
        let formData=new FormData()
        formData.append('registeredEmail',receiveDetailsName.current.value)
        formData.append('registeredPassword',receivePassword.current.value)
        formData.append('funcName','verifyLogin')
        let options={
            method:"POST",
            body:formData,
        }


      let detailsfromServer= await fetch("https://pulse.brninfotech.com/pulse/modules/admin/ValidateLogin.php",options)
      let detailsObj = await detailsfromServer.json()
       dispatch({type:"UserDetails",value:detailsObj})
       if(detailsObj.loggedIn==="yes"){
         navigate("home")
        }
      
    }
    
    return (
        <div>
            
            <Form style={{backgroundColor:"orange"}}>
            <h4 style={{textAlign:"center"}}>Sign In</h4>
                <Form.Group as={Row} className="mb-3" controlId="formPlaintextEmail">
                    <Form.Label column sm="2">
                        Email
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control type="email"  placeholder="email@example.com" ref={receiveDetailsName} />
                    </Col>
                </Form.Group>

                <Form.Group as={Row} className="mb-3" controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                        Password
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control type="password" placeholder="Password" ref={receivePassword}/>
                    </Col>
                </Form.Group>
                
                <Button variant="danger" onClick={()=>{
                  serverResponse()  
                }}>Login</Button>
                <input type="checkbox" />
                <label>Remember me</label>

                <label style={{color:"red"}}>Forget Password</label>
                
                <br></br>
                <Button type="button" style={{width:"25vw",margin:"0.2rem",position:"relative",left:"20vw"}}  >Create Account</Button>
            </Form>

                    
            <Modal.Dialog>
  <Modal.Header closeButton>
    <Modal.Title>Forget Password</Modal.Title>
  </Modal.Header>

  <Modal.Body>
  <Form.Label>Email address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" />
        
  </Modal.Body>

  <Modal.Footer>
    <Button variant="secondary">Back</Button>
    <Button variant="primary">submit</Button>
  </Modal.Footer>
</Modal.Dialog>
           
        </div>
    )
}