
import './App.css';
import {BrowserRouter,Routes,Route,NavLink} from "react-router-dom"
import Dailystatusupdate from './Screens/Dailystatusupdate';
import Home from './Screens/Home';
import Leave from './Screens/Leave';
import Login from './Screens/Login';
import Messages from './Screens/Messages';
import Request from './Screens/Request';

import Nopagefound from './Screens/Nopagefound';
import {Dropdown} from "react-bootstrap";
import TaskReview from './Screens/TaskReview';
import ManageTask from './Screens/ManageTask';
import React,{Suspense} from 'react';


function App() {
  const isActivefunction=(obj)=>{
    console.log(obj)
    if(obj.isActive === true){
      return {backgroundColor:"green",color:"red"}
    }
  }
  const LazyTasks=React.lazy(() => import('./Screens/Tasks'))
  const LazyTaskReview=React.lazy(()=>import ('./Screens/TaskCreation'))
  
 
  return (
    <BrowserRouter>
    
    <div className="Nav">
    <NavLink style={(obj)=>
      isActivefunction(obj)
    } to="/" >Login</NavLink>
    <NavLink to="home" style={(obj)=>
      isActivefunction(obj)
    } >Home</NavLink>
    <NavLink to="dailystatus" style={(obj)=>
      isActivefunction(obj)
    }>Dailystatusupdate</NavLink>
    <NavLink to="task" style={(obj)=>
      isActivefunction(obj)
    }>Task</NavLink>
    <Dropdown>
  <Dropdown.Toggle variant="success" id="dropdown-basic">
   MORE
  </Dropdown.Toggle>

  <Dropdown.Menu style={{backgroundColor:"hotpink"}}>
  <NavLink to="message" style={(obj)=>
      isActivefunction(obj)
    } >Messagae</NavLink>
  <br/>
    <NavLink to="request" style={(obj)=>
      isActivefunction(obj)
    }>Request</NavLink>
    <br/>
    <NavLink to="leave" style={(obj)=>
      isActivefunction(obj)}>Leave</NavLink>
    </Dropdown.Menu>
</Dropdown>
   
    </div>
    <Suspense fallback={<div>Loading....</div>}>
    <Routes>
      <Route path="/" element={<Login/>}></Route>
      <Route path="home" element={<Home/>}></Route>
      <Route path="dailystatus" element={<Dailystatusupdate/>}></Route>
      <Route path="task" element={<LazyTasks/>}>
      <Route path="taskCreation" element={<LazyTaskReview/>}></Route>
        <Route path="taskCreation/:studentName/:age/:Location" element={<LazyTaskReview/>}></Route>
        <Route path="taskReview" element={<TaskReview/>}></Route>
        <Route path="managetasks" element={<ManageTask/>}></Route>
      </Route>
      <Route path="message" element={<Messages/>}></Route>
      <Route path="request" element={<Request/>}></Route>
      <Route path="leave" element={<Leave/>}></Route>
      <Route path="*" element={<Nopagefound/>}></Route>
    
    </Routes>
    </Suspense>
    </BrowserRouter>
  );
}

export default App;
