import React from 'react';
import {Form,Button, FormGroup,Row} from "react-bootstrap";
import { useSelector } from 'react-redux';
import {Navigate} from 'react-router-dom';

export default function Request() {
  let storeDetails=useSelector((store)=>store)
  if(storeDetails.userDetails.loggedIn==="yes"){
    console.log("you visit this Page")
  }else{
    console.log("You Wont Visit this Page")
    return <Navigate to="/"/>
  }
  return (
    <div>
        <Form>
            <h6 style={{backgroundColor:"orange",textAlign:"center"}}>Request</h6>
        <FormGroup as={Row} className="mb-3" controlId="formPlaintextEmail">
        <Form.Label>Request Type*:</Form.Label>
        <Form.Select aria-label="Default select example">
  <option>Open this select menu</option>
  <option value="1">One</option>
  <option value="2">Two</option>
  <option value="3">Three</option>
</Form.Select>
</FormGroup>
<FormGroup as={Row} className="mb-3" controlId="formPlaintextEmail">
<Form.Label>Lab Id*:</Form.Label>
        <Form.Select aria-label="Default select example">
  <option>Open this select menu</option>
  <option value="1">One</option>
  <option value="2">Two</option>
  <option value="3">Three</option>
</Form.Select>
</FormGroup>
<FormGroup as={Row} className="mb-3" controlId="formPlaintextEmail">
<Form.Label>Serial Number*:</Form.Label>
        <Form.Select aria-label="Default select example">
  <option>Open this select menu</option>
  <option value="1">One</option>
  <option value="2">Two</option>
  <option value="3">Three</option>
</Form.Select>
<Form.Label>Description*:</Form.Label>
<Form.Control as="textarea" aria-label="With textarea"/>
</FormGroup>
<Button type="submit">submit</Button>

</Form>
    </div>
  )
}
