import React from 'react';
import {Table,Form,Button} from "react-bootstrap";
import {Navigate} from "react-router-dom";
import {useSelector} from "react-redux"

function Dailystatusupdate() {
  let storeDetails=useSelector((store)=>store)
  if(storeDetails.userDetails.loggedIn==="yes"){
    console.log("you visit this Page")
  }else{
    console.log("You Wont Visit this Page")
    return <Navigate to="/"/>
  }
  return (
    <div>
        
        <Table striped bordered hover>
    
     <tbody>
    <tr>
      <td><Form.Label>Morning 9:00-1:00</Form.Label></td>
      <td><Form.Control type="text" placeholder="Morning 9-1AM"/></td>
       </tr>
       <tr>
      <td><Form.Label>Afternoon 1:00-4:00</Form.Label></td>
      <td><Form.Control type="text" placeholder="Afternoon 1-4PM"/></td>
       </tr>
       <tr>
      <td><Form.Label>Evening 4:00-8:00</Form.Label></td>
      <td><Form.Control type="text" placeholder="Evevning 4-8PM"/></td>
       </tr>
      
  </tbody>
  </Table>
  <Button type="button" variant='secondary' style={{position:"relative",left:"20vw",color:"red",margin:"0.5rem"}}>UpdateStatus</Button>


    </div>
  )
}

export default Dailystatusupdate